@import "normalize.css";
@import "weui.min.css";
* {
  font-family: "Microsoft YaHei", '微软雅黑';
}
html,
body {
  background: #f5f5f9;
  max-width: 768px;
  min-width: 320px;
  margin: 0 auto;
}
.icon {
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.logo {
  padding-top: 0.3rem;
}
.logo img {
  display: block;
  margin: 0 auto;
  width: 2.62rem;
}
.weui-toast {
  width: 2.4rem;
  min-height: 2.4rem;
  margin-left: -1.2rem;
  top: 35%;
}
.weui-toast i {
  margin: 0.3rem 0 0;
}
.weui-toast i:before {
  font-size: 1rem !important;
}
.weui-toast .weui-toast__content {
  font-size: 0.34rem;
  margin: 0.3rem 0 0 0;
}
.mt10 {
  margin-top: 0.1rem;
}
.mb20 {
  margin-bottom: 0.2rem !important;
}
.weui-cell_select .weui-cell__bd {
  position: relative;
}
.weui-cell_select .weui-cell__bd p {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  left: 0;
  color: #b2b2b2;
}
.weui-switch,
.weui-switch-cp__box {
  background-color: #fff;
  width: 1.04rem;
  height: 0.64rem;
  border: 0.01rem solid #dfdfdf;
  border-radius: 0.64rem;
}
.weui-switch-cp__input:checked ~ .weui-switch-cp__box,
.weui-switch:checked {
  border-color: #4499ff;
  background-color: #4499ff;
}
.weui-switch-cp__box:before,
.weui-switch:before {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 0.3rem;
}
.weui-switch-cp__box:after,
.weui-switch:after {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 0.3rem;
}
.weui-switch-cp__input:checked ~ .weui-switch-cp__box:after,
.weui-switch:checked:after {
  transform: translateX(0.4rem);
}
.weui-cell__bd_radio {
  overflow: inherit !important;
}
.mint-radiolist {
  line-height: 1;
}
.mint-radiolist label {
  margin: 0;
}
.mint-radiolist .mint-cell {
  display: inline-block;
  padding-right: 0.15rem;
  min-height: 0.48rem;
  background-image: none !important;
}
.mint-radiolist .mint-cell:last-child {
  padding: 0;
}
.mint-radiolist .mint-cell .mint-cell-wrapper {
  padding: 0;
  background-image: none !important;
  color: #b2b2b2;
}
.mint-radiolist .mint-cell .mint-cell-wrapper .mint-radiolist-label {
  padding: 0;
}
.mint-radiolist .mint-cell .mint-radio-label {
  white-space: nowrap;
  margin: 0;
  padding-top: 1px;
  display: inline-block;
}
.mint-radiolist .mint-cell .mint-radiolist-label {
  width: auto !important;
}
.mint-radiolist .mint-cell .mint-radio-core {
  width: 0.3rem;
  height: 0.3rem;
}
.mint-radiolist .mint-cell .mint-radio-core::after {
  top: 0.06rem;
  left: 0.06rem;
  width: 0.15rem;
  height: 0.15rem;
}
.weui-cell .weui-cell__ft:after,
.weui-cell_select .weui-cell__bd:after {
  width: 0.1rem;
  height: 0.1rem;
}
.weui-cells {
  margin: 0;
}
.mode .weui-cell__hd {
  font-size: 0.3rem;
  color: #2b2b2b;
  width: 1.8rem;
}
.mode .weui-cell__bd {
  overflow: initial !important;
  color: #2b2b2b;
}
.downDialog .downDialog-top {
  text-align: center;
  padding: 0;
  line-height: 0.9rem;
  background: #efefef;
  font-size: 0.3rem;
  color: #333333;
}
.downDialog .downDialog-top i {
  color: #818fa4;
  position: absolute;
  top: 0.2rem;
  right: 0.2rem;
  z-index: 2;
  font-size: 0.5rem;
}
.downDialog .Dialogbody {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 4;
}
.information {
  color: #2b2b2b;
  margin: 0;
}
.information .weui-cell {
  height: 0.87rem;
  padding: 0 0.2rem;
}
.information .weui-cell svg {
  font-size: 0.35rem;
  vertical-align: middle;
  margin-right: 0.25rem;
  color: #8492a6;
  font-weight: 500;
}
.information .weui-cell .icon {
  width: 0.36rem;
  height: 0.36rem;
}
.information .weui-cell p {
  font-size: 0.24rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.information .details .weui-cell__hd .icon:last-child {
  margin: 0;
}
.information .weui-cell__ft {
  color: #4499ff;
  font-size: 0.2rem;
  white-space: nowrap;
  padding-right: 0.2rem;
}
.information:before {
  border-top: none;
}
.Book {
  padding-bottom: 1rem;
}
.Book .mint-tab-container {
  padding-bottom: 1rem;
}
.Book #banner img {
  width: 100%;
}
.Book #banner .swiper-pagination {
  bottom: 0.15rem;
  line-height: 0;
}
.Book #banner .swiper-pagination .swiper-pagination-bullet {
  width: 0.1rem;
  height: 0.1rem;
  background: #999999;
  opacity: 1;
  border-radius: 0.05rem;
}
.Book #banner .swiper-pagination .swiper-pagination-bullet-active {
  background: #3688ff;
  width: 0.2rem;
}
.Book .banner {
  position: relative;
}
.Book .banner .text {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  line-height: 0;
  height: 0.8rem;
  background: rgba(146, 146, 146, 0.4);
  overflow: hidden;
}
.Book .banner svg {
  width: 0.3rem;
  height: 0.3rem;
  vertical-align: initial;
  margin-left: 0.15rem;
}
.Book .banner marquee {
  font-size: 0.3rem;
  color: #fff;
  line-height: 0.8rem;
  position: absolute;
  left: 0.7rem;
  width: 88%;
}
.Book .activeRegion {
  height: 1.65rem;
  margin: 0.15rem 0;
  box-shadow: 0px 0px 9px rgba(3, 2, 3, 0.1);
  background: #f4f4f4;
}
.Book .activeRegion img {
  width: 1.65rem;
  display: block;
  margin-right: 0.2rem;
}
.Book .activeRegion .swiper-slide {
  width: 6.5rem;
  border-radius: 5px;
  overflow: hidden;
  background: #fff;
}
.Book .activeRegion .weui-cell {
  padding: 0;
  background: #fff;
  font-size: 0.28rem;
  color: #b2b2b2;
}
.Book .activeRegion .title {
  font-size: 0.28rem;
  color: #2b2b2b;
}
.Book .activeRegion .title span {
  display: block;
  float: right;
  line-height: 1;
  padding: 0.1rem 0.1rem;
  margin-right: 0.15rem;
  font-size: 0.2rem;
  color: #fff;
  border-radius: 3px;
}
.Book .activeRegion .title .Hour {
  background: linear-gradient(to right, #ffa44d, #fb731d);
}
.Book .activeRegion .title .special {
  background: linear-gradient(to right, #fb2c9d, #ed1c53);
}
.Book .activeRegion .title .Seckill {
  background: linear-gradient(to right, #908ff6, #5f65f2);
}
.Book .activeRegion .newPrice {
  font-size: 0.36rem;
  color: #ff4949;
  font-weight: bold;
}
.Book .activeRegion .newPrice i {
  font-style: normal;
  font-size: 0.24rem;
}
.Book .timebox {
  font-size: 0.22rem;
  position: relative;
  color: #b2b2b2;
  margin-top: 0.15rem;
}
.Book .timebox:before {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #d9d9d9;
  color: #d9d9d9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.Book .timebox .weui-flex__item {
  height: 0.9rem;
  line-height: 0.9rem;
  position: relative;
  padding: 0 0.2rem;
  background: #fff;
}
.Book .timebox .weui-flex__item:before {
  content: " ";
  display: inline-block;
  height: 0.1rem;
  width: 0.1rem;
  border-width: 2px 2px 0 0;
  border-color: #c8c8cd;
  border-style: solid;
  -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  position: relative;
  top: -2px;
  position: absolute;
  top: 50%;
  margin-top: -4px;
  right: 0.25rem;
}
.Book .timebox .weui-flex__item:after {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 1px;
  border-right: 1px solid #d9d9d9;
  color: #d9d9d9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleX() 0.5;
  transform: scaleX(0.5);
}
.Book .timebox .time {
  font-size: 0.28rem;
  color: #2b2b2b;
  display: inline-block;
  margin-right: 0.2rem;
}
.Book .timebox .fr {
  margin-right: 0.3rem;
}
.Activitydetails {
  padding-bottom: 1rem;
}
.Activitydetails .regular {
  color: #b2b2b2;
  font-size: 0.28rem;
  line-height: 0.36rem;
  padding: 0.2rem 0.15rem;
  background: #fff;
}
.Activitydetails .regular strong {
  color: #333333;
  font-weight: normal;
}
.BookImmediately {
  font-size: 0.3rem;
}
.BookImmediately .title {
  background: #fff;
  padding: 0.15rem 0.25rem;
}
.BookImmediately .title h3 {
  color: #2b2b2b;
  font-size: 0.3rem;
  line-height: 0.3rem;
  margin-bottom: 0.15rem;
  font-weight: bold;
}
.BookImmediately .title p {
  color: #b2b2b2;
  font-size: 0.24rem;
  line-height: 0.24rem;
}
.BookImmediately .title p span {
  display: inline-block;
  margin-right: 0.25rem;
}
.BookImmediately .weui-cells_form .weui-cell {
  padding: 0.15rem 0.25rem;
  padding-right: 0;
}
.BookImmediately .weui-cells_form .weui-cell_switch {
  padding-right: 15px;
}
.BookImmediately .weui-cells_form label,
.BookImmediately .weui-cells_form .weui-cell__bd {
  font-size: 0.3rem;
  color: #2b2b2b;
  width: 1.8rem;
}
.BookImmediately .weui-cells_form input {
  font-size: 0.3rem;
}
.BookImmediately .weui-cells_form .Prompt {
  font-size: 0.2rem;
  color: #b2b2b2;
}
.BookImmediately .weui-cells_form .weui-cell_select label,
.BookImmediately .weui-cells_form .weui-cell_switch label {
  width: 6rem;
}
.BookImmediately .weui-cells_form .red {
  color: #ff4949;
}
.BookImmediately .Settlement {
  background: #fff;
  padding: 5px 0;
  margin-bottom: 0.2rem;
}
.BookImmediately .Settlement .weui-cells:after,
.BookImmediately .Settlement .weui-cells:before {
  border: none;
}
.BookImmediately .Settlement .weui-cell {
  padding: 0rem 15px 0 0.25rem;
  font-size: 0.28rem;
  line-height: 0.45rem;
}
.BookImmediately .Settlement .weui-cell .weui-cell__ft {
  color: #ff4949;
}
.BookImmediately .placeOrder {
  height: 1rem;
  line-height: 1rem;
  background: #fff;
}
.BookImmediately .placeOrder .placeOrder-text {
  overflow: hidden;
  text-align: right;
  font-size: 0.28rem;
  padding-right: 0.25rem;
}
.BookImmediately .placeOrder .red {
  color: #ff4949;
}
.BookImmediately .placeOrder a {
  line-height: 1rem;
  height: 100%;
  background: #4499ff;
  border-radius: 0;
  font-size: 0.28rem;
}
.BookImmediately .placeOrder .weui-btn:after {
  border: 0;
}
.BookImmediately .Coupons {
  background: #fff7eb;
  font-size: 0;
  padding: 0.4rem 0 0 0.5rem;
}
.BookImmediately .Coupons .Coupon {
  display: inline-block;
  font-size: 0.3rem;
  width: 3.06rem;
  height: 0.85rem;
  line-height: 0.85rem;
  position: relative;
  text-align: center;
  margin-bottom: 0.4rem;
  margin-right: 0.3rem;
  border-radius: 7px;
  color: #f10000;
}
.BookImmediately .Coupons .Coupon svg {
  display: inline-block;
  height: 0.6rem;
  width: 0.6rem;
  vertical-align: middle;
}
.BookImmediately .Coupons .Coupon span {
  color: #000000;
}
.BookImmediately .Coupons .Coupon:after {
  content: " ";
  width: 200%;
  height: 200%;
  position: absolute;
  top: 0;
  left: 0;
  border: 2px solid #ff7d13;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  box-sizing: border-box;
  border-radius: 10px;
}
.BookImmediately .Coupons .Coupon.active {
  background: #ff7d13;
  color: #fff;
}
.BookImmediately .Coupons .Coupon.active span {
  color: #fff;
}
.Payment .weui-cells_form .weui-cell {
  padding: 0.2rem 0.2rem;
}
.Payment .weui-cells_form .weui-label {
  width: 1.60rem;
  font-size: 0.3rem;
  color: #b2b2b2;
}
.Payment .weui-cells_form .weui-cell__bd {
  font-size: 0.3rem;
  color: #2b2b2b;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.Payment .weui-cells_form .weui-cell__bd .time {
  display: inline-block;
  margin-left: 0.2rem;
}
.Payment .weui-cells_form .information:before {
  border-top: 1px solid #d9d9d9;
}
.Payment .weui-cells_form .weui-flex {
  padding: 0.3rem 0.25rem;
}
.Payment .weui-flex {
  display: flex;
  position: relative;
  color: #b2b2b2;
  font-size: 0.3rem;
}
.Payment .weui-flex::after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #d9d9d9;
  color: #d9d9d9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  left: 15px;
}
.Payment .weui-flex .foodname {
  width: 2.3rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.Payment .weui-flex .money {
  width: 2rem;
  color: #ff4949;
  text-align: right;
}
.Payment .weui-flex .weui-flex__item {
  flex: 1;
}
.Payment .weui-flex .weui-flex__item .placeholder span {
  display: inline-block;
  margin-left: 0.4rem;
}
.Payment .totalSum {
  padding: 0.2rem 0 0.3rem 0.3rem;
  font-size: 0.3rem;
  background: #f4f4f4;
  color: #2b2b2b;
}
.Payment .totalSum p {
  line-height: 0.54rem;
  font-weight: bold;
}
.Payment .totalSum p .weui-btn {
  display: inline-block;
  width: 1.4rem;
  height: 0.54rem;
  padding: 0;
  line-height: 0.54rem;
  color: #fff;
  font-size: 0.24rem;
  background: #ff7d13;
  vertical-align: bottom;
  border-radius: 3px;
  letter-spacing: 2px;
}
.Payment .totalSum p .weui-btn:after {
  display: none;
}
.Payment .totalSum .Price {
  color: #ff4949;
}
.Payment .totalSum .minutia {
  color: #4499ff;
  display: inline-block;
  margin-left: 0.1rem;
}
.Payment .totalSum .btn {
  font-size: 0;
}
.Payment .totalSum .btn a {
  width: 3.35rem;
  display: inline-block;
  height: 0.68rem;
  font-size: 0.3rem;
  line-height: 0.68rem;
  background: #4499ff;
  margin-top: 0.2rem;
}
.Payment .totalSum .btn a:after {
  display: none;
}
.Payment .totalSum .btn .weui-btn + .weui-btn {
  margin-left: 0.2rem;
  background: #d1d1d1;
}
.MealList .weui-flex {
  text-align: center;
  position: relative;
  background: #fff;
}
.MealList .weui-flex::after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #f6f6f6;
  color: #f6f6f6;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.MealList .weui-flex .placeholder {
  font-size: 0.34rem;
  color: #5f646e;
  font-weight: bold;
  height: 1.06rem;
  line-height: 1.06rem;
  border-bottom: 2px solid transparent;
  transition: all 0.3s;
