@import "./normalize.css";
@import "./weui.min.css";
*{
     font-family: "Microsoft YaHei",'微软雅黑';
}
html,body{
    background: #f5f5f9;
    max-width:768px;
    min-width:320px;
    margin: 0 auto;
}
.icon {
   vertical-align: -0.15em;
   fill: currentColor;
   overflow: hidden;
}
.logo{
    padding-top: 0.3rem;
    img{
        display: block;
        margin: 0 auto;
        width: 2.62rem;
    }
}
.weui-toast{
    width: 2.4rem;
    min-height: 2.4rem;
    margin-left:-1.2rem;
    top: 35%;
    i{
        margin: 0.3rem 0 0;
        &:before{
            font-size: 1rem !important;
        }
    }
    .weui-toast__content{
        font-size: 0.34rem;
        margin: 0.3rem 0 0 0;
    }
}
.mt10{
    margin-top: 0.1rem;
}
.mb20{
    margin-bottom:0.2rem !important;
}
.weui-cell_select{
    .weui-cell__bd{
        position: relative;
        p{
            position: absolute;
            top: 50%;
            transform: translate(0,-50%);
            left: 0;
            color: #b2b2b2;
        }
    }
}
.weui-switch, .weui-switch-cp__box{
    background-color: #fff;
    width: 1.04rem;
    height: 0.64rem;
    border: 0.01rem solid #dfdfdf;
    border-radius: 0.64rem;
}
.weui-switch-cp__input:checked~.weui-switch-cp__box, .weui-switch:checked{
    border-color: #4499ff;
    background-color: #4499ff;
}
.weui-switch-cp__box:before, .weui-switch:before{
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 0.3rem;
}
.weui-switch-cp__box:after, .weui-switch:after{
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 0.3rem;
}
.weui-switch-cp__input:checked~.weui-switch-cp__box:after, .weui-switch:checked:after{
        transform: translateX(0.4rem);
}
.weui-cell__bd_radio{
    overflow: inherit !important;
}
.mint-radiolist{
    line-height: 1;
    label{
        margin: 0;
    }
    .mint-cell{
        display: inline-block;
        padding-right: 0.15rem;
        min-height:0.48rem;
        background-image: none !important;
        &:last-child{
            padding: 0;
        }
        .mint-cell-wrapper{
            padding: 0;
            background-image: none !important;
            color: #b2b2b2;
            .mint-radiolist-label{
                padding: 0;
            }
        }
        .mint-radio-label{
            white-space: nowrap;
            margin: 0;
            padding-top: 1px;
            display: inline-block;
        }
        .mint-radiolist-label{
            width: auto !important;
        }
        .mint-radio-core{
            width: 0.3rem;
            height: 0.3rem;
        }
        .mint-radio-core::after{
            top: 0.06rem;
            left: 0.06rem;
            width: 0.15rem;
            height: 0.15rem;
        }

    }
}
.weui-cell .weui-cell__ft:after,.weui-cell_select .weui-cell__bd:after{
    width: 0.1rem;
    height: 0.1rem;
}
.weui-cells{
    margin: 0;
}
.mode{
    .weui-cell__hd{
        font-size: 0.3rem;
        color: #2b2b2b;
        width: 1.8rem;
    }
    .weui-cell__bd{
        overflow: initial !important;
        color: #2b2b2b;
    }
}

.downDialog{
    //向上的弹框头部
    .downDialog-top {
        text-align: center;
        padding: 0;
        line-height: 0.9rem;
        background: #efefef;
        font-size: 0.3rem;
        color: #333333;
        i {
            color: #818fa4;
            position: absolute;
            top: 0.2rem;
            right: 0.2rem;
            z-index: 2;
            font-size: 0.5rem;
        }
    }
    .Dialogbody{
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 4;
    }
}
.information{
    color: #2b2b2b;
    margin: 0;
    .weui-cell{
        height: 0.87rem;
        padding: 0 0.2rem;
        svg{
            font-size: 0.35rem;
            vertical-align: middle;
            margin-right: 0.25rem;
            color: #8492a6;
            font-weight: 500;
        }
        .icon{
            width: 0.36rem;
            height: 0.36rem;
        }
        p{
            font-size: 0.24rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    .details{
        .weui-cell__hd{
           .icon:last-child{
               margin: 0;
           }
        }
    }
    .weui-cell__ft{
        color: #4499ff;
        font-size: 0.2rem;
        white-space: nowrap;
        padding-right: 0.2rem;
    }
    &:before{
        border-top:none;
    }
}
.Book{
    padding-bottom: 1rem;
    .mint-tab-container{
        padding-bottom: 1rem;
    }
    #banner{
        img {
            width: 100%;
        }
        .swiper-pagination{
            bottom: 0.15rem;
            line-height: 0;
            .swiper-pagination-bullet {
                width:0.1rem;
                height: 0.1rem;
                background: #999999;
                opacity:1;
                border-radius:0.05rem;

            }
            .swiper-pagination-bullet-active{
                background: #3688ff;
                width: 0.2rem;
            }
        }
    }
    .banner {
        position: relative;
        .text{
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 2;
            line-height: 0;
            height: 0.8rem;
            background: rgba(146,146,146,0.4);
            overflow: hidden;
        }
        svg{
            width: 0.3rem;
            height:0.3rem;
            vertical-align: initial;
            margin-left: 0.15rem;
        }
        marquee{
            font-size: 0.3rem;
            color: #fff;
            line-height: 0.8rem;
            position: absolute;
            left: 0.7rem;
            width: 88%;
        }
    }
    .activeRegion{
        height: 1.65rem;
        margin: 0.15rem 0;
        box-shadow: 0px 0px 9px rgba(3, 2, 3, 0.1); // inset
         background: #f4f4f4;
        img{
            width: 1.65rem;
            display: block;
            margin-right: 0.2rem;
        }
        .swiper-slide{
            width:6.5rem;
            border-radius: 5px;
            overflow: hidden;
            background: #fff;
        }
        .weui-cell{
            padding: 0;
            background: #fff;
            font-size: 0.28rem;
            color: #b2b2b2;
        }
        .title{
            font-size: 0.28rem;
            color: #2b2b2b;
            span{
                display: block;
                float: right;
                line-height: 1;
                padding:0.1rem 0.1rem;
                margin-right: 0.15rem;
                font-size: 0.2rem;
                color: #fff;
                border-radius:3px;
            }
            .Hour{
                background: linear-gradient(to right, #ffa44d , #fb731d);

            }
            .special{
                background: linear-gradient(to right, #fb2c9d , #ed1c53);
            }
            .Seckill{
                background: linear-gradient(to right, #908ff6 , #5f65f2);
            }
        }
        .newPrice{
            font-size: 0.36rem;
            color: #ff4949;
            font-weight: bold;
            i{
                font-style: normal;
                font-size: 0.24rem;
            }
        }

    }
    .timebox{
        font-size:0.22rem;
        position: relative;
        color: #b2b2b2;
        margin-top: 0.15rem;
        &:before{
            content: " ";
            position: absolute;
            left: 0;
            bottom: 0;
            right: 0;
            height: 1px;
            border-top: 1px solid #d9d9d9;
            color: #d9d9d9;
            -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
            -webkit-transform: scaleY(.5);
            transform: scaleY(.5);
        }
        .weui-flex__item{
            height:0.9rem;
            line-height: 0.9rem;
            position: relative;
            padding: 0 0.2rem;
            background: #fff;
            &:before{
                content: " ";
                display: inline-block;
                height: 0.1rem;
                width: 0.1rem;
                border-width: 2px 2px 0 0;
                border-color: #c8c8cd;
                border-style: solid;
                -webkit-transform: matrix(.71,.71,-.71,.71,0,0);
                transform: matrix(.71,.71,-.71,.71,0,0);
                position: relative;
                top: -2px;
                position: absolute;
                top: 50%;
                margin-top: -4px;
                right: 0.25rem;
            }
            &:after{
                content: " ";
                position: absolute;
                right:0;
                top: 0;
                bottom: 0;
                width: 1px;
                border-right: 1px solid #d9d9d9;
                color: #d9d9d9;
                -webkit-transform-origin: 0 0;
                transform-origin: 0 0;
                -webkit-transform: scaleX()(.5);
                transform: scaleX(.5);
            }
        }
        .time{
            font-size: 0.28rem;
            color: #2b2b2b;
            display: inline-block;
            margin-right: 0.2rem;
        }
        .fr{
            margin-right: 0.3rem;
        }
    }
}
.Activitydetails{
    padding-bottom: 1rem;
    .regular{
        color: #b2b2b2;
        font-size: 0.28rem;
        line-height: 0.36rem;
        padding: 0.2rem 0.15rem;
        background: #fff;
        strong{
            color: #333333;
            font-weight: normal;
        }
    }
}
.BookImmediately{
    font-size: 0.3rem;
    .title{
        background: #fff;
         padding: 0.15rem 0.25rem;
        h3{
            color: #2b2b2b;
            font-size: 0.3rem;
            line-height: 0.3rem;
            margin-bottom: 0.15rem;
            font-weight: bold;
        }
        p{
            color: #b2b2b2;
            font-size: 0.24rem;
             line-height: 0.24rem;
             span{
                 display: inline-block;
                 margin-right: 0.25rem;
             }
        }
    }
    .weui-cells_form{
        .weui-cell{
             padding: 0.15rem 0.25rem;
            padding-right: 0;
        }
        .weui-cell_switch{
            padding-right: 15px;
        }
        label,.weui-cell__bd{
            font-size: 0.3rem;
            color: #2b2b2b;
            width: 1.8rem;
        }
        input{
            font-size: 0.3rem;
        }
        .Prompt{
            font-size: 0.2rem;
            color: #b2b2b2;
        }
        .weui-cell_select,.weui-cell_switch{
            label{
                width: 6rem;
            }
        }
        .red{
            color: #ff4949;
        }
    }
    .Settlement{
        background: #fff;
        padding: 5px 0;
        margin-bottom: 0.2rem;
        .weui-cells:after, .weui-cells:before{
            border:none;
        }
        .weui-cell{
            padding: 0rem 15px 0 0.25rem;
            font-size: 0.28rem;
            line-height: 0.45rem;
            .weui-cell__ft{
                color: #ff4949;
            }
        }
    }
    .placeOrder{
        height: 1rem;
        line-height: 1rem;
        background: #fff;
        .placeOrder-text{
            overflow: hidden;
            text-align: right;
            font-size: 0.28rem;
            padding-right: 0.25rem;
        }
        .red{
            color: #ff4949;
        }
        a{
            line-height: 1rem;
            height: 100%;
            background: #4499ff;
            border-radius: 0;
            font-size: 0.28rem;
        }
        .weui-btn:after{
            border: 0;
        }
    }
    .Coupons{
        background: #fff7eb;
        font-size: 0;
        padding: 0.4rem 0 0 0.5rem;
        .Coupon{
            display: inline-block;
            font-size: 0.3rem;
            width: 3.06rem;
            height: 0.85rem;
            line-height: 0.85rem;
            position: relative;
            text-align: center;
            margin-bottom: 0.4rem;
            margin-right: 0.3rem;
            border-radius: 7px;
            color:#f10000;
            svg{
                display: inline-block;
                height: 0.6rem;
                width: 0.6rem;
                // margin-top: 0.15rem;
                vertical-align: middle;
            }
            span{
                color:#000000;
            }
            &:after {
                content: " ";
                width: 200%;
                height: 200%;
                position: absolute;
                top: 0;
                left: 0;
                border: 2px solid #ff7d13;
                -webkit-transform: scale(.5);
                transform: scale(.5);
                -webkit-transform-origin: 0 0;
                transform-origin: 0 0;
                box-sizing: border-box;
                border-radius: 10px;
            }
        }
        .Coupon.active{
            background: #ff7d13;
            color: #fff;
            span{
                color:#fff;
            }
        }
    }
}
.Payment{
    .weui-cells_form{
        .weui-cell{
            padding: 0.2rem 0.2rem;
        }
        .weui-label{
            width: 1.60rem;
            font-size: 0.3rem;
            color: #b2b2b2;
        }
        .weui-cell__bd{
            font-size: 0.3rem;
            color: #2b2b2b;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            .time{
                display: inline-block;
                margin-left: 0.2rem;
            }
        }
        .information:before{
            border-top: 1px solid #d9d9d9;
        }
        .weui-flex{
            padding: 0.3rem 0.25rem;
        }
    }
    .weui-flex{
        display: flex;
        position: relative;
        color: #b2b2b2;
        font-size: 0.3rem;
        &::after{
            content: " ";
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            height: 1px;
            border-top: 1px solid #d9d9d9;
            color: #d9d9d9;
            -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
            -webkit-transform: scaleY(.5);
            transform: scaleY(.5);
            left: 15px;
        }
        .foodname{
            width: 2.3rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        .money{
           width: 2rem;
           color: #ff4949;
           text-align: right;
        }
        .weui-flex__item{
            flex: 1;
            .placeholder{
                span{
                    display: inline-block;
                    margin-left: 0.4rem;
                }
            }
        }
    }
    .totalSum{
        padding: 0.2rem 0 0.3rem 0.3rem;
        font-size: 0.3rem;
        background: #f4f4f4;
        color: #2b2b2b;
        p{
            line-height: 0.54rem;
            font-weight: bold;
            .weui-btn{
                display: inline-block;
                width: 1.4rem;
                height: 0.54rem;
                padding: 0;
                line-height: 0.54rem;
                color: #fff;
                font-size: 0.24rem;
                background: #ff7d13;
                vertical-align: bottom;
                border-radius: 3px;
                letter-spacing:2px;
                &:after{
                    display: none;
                }
            }
        }
        .Price{
            color: #ff4949;
        }
        .minutia{
            color: #4499ff;
            display: inline-block;
            margin-left: 0.1rem;
        }
        .btn{
            font-size: 0;
            a{
                width: 3.35rem;
                display: inline-block;
                height: 0.68rem;
                font-size: 0.3rem;
                line-height: 0.68rem;
                background: #4499ff;
                margin-top: 0.2rem;
                &:after{
                    display: none;
                }
            }
            .weui-btn+.weui-btn{
                margin-left: 0.2rem;
                background: #d1d1d1;
            }
        }
    }
}
.MealList{
    .weui-flex{
        text-align: center;
        position: relative;
        background: #fff;
        &::after{
            content: " ";
            position: absolute;
            left: 0;
            bottom: 0;
            right: 0;
            height: 1px;
            border-bottom: 1px solid #f6f6f6;
            color: #f6f6f6;
            -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
            -webkit-transform: scaleY(.5);
            transform: scaleY(.5);
        }
        .placeholder{
            font-size: 0.34rem;
            color: #5f646e;
            font-weight: bold;
            height: 1.06rem;
            line-height: 1.06rem;
            border-bottom: 2px solid transparent;
            transition: all 0.3s;
        }
        .active.placeholder{
            color: #4499ff;
            border-bottom: 2px solid #4499ff;
        }
    }
    .tabcontent{
        padding-bottom: 1rem;
        .weui-cell{
            padding: 0.15rem 0.35rem 0.15rem 0.1rem;
        }
        line-height: 0;
        img{
            width: 1.8rem;
            height: 1.8rem;
            display: block;
            border-radius: 3px;
            overflow: hidden;
        }
        h3{
            padding-top: 1px;
            font-size: 0.34rem;
            color: #333333;
            font-weight: bold;
            white-space: nowrap;
            overflow: hidden;
            width: 3.2rem;
            text-overflow: ellipsis;
            line-height: 1;
            margin-bottom: 0.2rem;
        }
        p{
            color: #b2b2b2;
            font-size: 0.3rem;
            padding: 1px 0;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            line-height: 1;
        }
        .time{
            margin-top: 0.65rem;
        }
        .weui-cell__ft{
            font-size: 0;
            padding-bottom: 0.4rem;
            .Price{
                font-size: 0.24rem;
                color: #ff4949;
                font-weight: bold;
                margin-bottom: 0.4rem;
                span{
                   font-size: 0.36rem;
                }
            }

        }
    }
    .tabcontent,.Shopping{
        .Calculation{
            padding: 0;
            height: 0.48rem;
            .minus{
                color: #4499ff;
                background: none;
                position: relative;
                width: 0.48rem;
                height: 0.48rem;
                line-height: 0.48rem;
                &:after {
                    content: " ";
                    width: 200%;
                    height: 200%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    border: 2px solid #4499ff;
                    -webkit-transform: scale(0.5);
                    transform: scale(0.5);
                    -webkit-transform-origin: 0 0;
                    transform-origin: 0 0;
                    box-sizing: border-box;
                    border-radius: 50%;
                }
            }
            .add{
                width: 0.48rem;
                height: 0.48rem;
                line-height: 0.48rem;
            }
            .num{
                height: 0.48rem;
                line-height: 0.48rem;
                color: #333333;
                background: none;
                margin: 0;
                padding: 0;
                width: 0.6rem;
                min-width:auto;
            }
        }
    }
    .mask{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0,0,0,0.5)
    }
    .Shopping{
        position: fixed;
        background: #fff;
        left: 0;
        right: 0;
        bottom: 0;
        height: 1rem;
        z-index: 9999;
        &:after{
            content: " ";
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            height: 1px;
            border-top: 1px solid #d9d9d9;
            color: #d9d9d9;
            -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
            -webkit-transform: scaleY(.5);
            transform: scaleY(.5);
        }
        a{
            line-height: 1rem;
            height: 100%;
            background: #4499ff;
            border-radius: 0;
            font-size: 0.3rem;
            padding: 0;
            width: 2.2rem;
        }
        .car{
            width: 1rem;
            height: 1rem;
            background: #4499ff;
            color: #fff;
            border-radius: 50%;
            line-height: 1rem;
            text-align: center;
            position: relative;
            top: -0.2rem;
            left: 0.2rem;
            z-index: 3;
            svg{
                width: 0.6rem;
                height: 0.6rem;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
            }
            span{
                position:absolute;
                top: 0;
                right: 0;
                height: 0.3rem;
                min-width: 0.3rem;
                padding: 0 0.05rem;
                box-sizing: border-box;
                background: #fb4e44;
                font-size: 0.24rem;
                color: #fff;
                line-height: 0.3rem;
                border-radius: 0.15rem;
            }

        }
        .Price{
            line-height: 1rem;
            color: #ff4949;
            font-size: 0.42rem;
            margin-left: 0.4rem;
        }
        .Shopping-details{
            position: absolute;
            bottom: 1rem;
            left: 0;
            right: 0;
            background: #fff;
                z-index: -1;
            .top{
                color: #666666;
                font-size: 0.28rem;
                line-height: 0.7rem;
                height: 0.7rem;
                text-align: right;
                padding: 0 0.25rem;
                background: #f4f4f4;
                svg{
                    width: 0.35rem;
                    height: 0.35rem;
                    vertical-align: middle;
                }
            }
            .list{
                font-size: 0.27rem;
                li{
                    padding:0.35rem 0.15rem;
                    height: 1.18rem;
                    position: relative;
                    &:after{
                        content: " ";
                        position: absolute;
                        top: 0;
                        right: 0;
                        width: 100%;
                        height: 1px;
                        border-top: 1px solid #d9d9d9;
                        color: #d9d9d9;
                        -webkit-transform-origin: 0 0;
                        transform-origin: 0 0;
                        -webkit-transform: scaleY(.5);
                        transform: scaleY(.5);
                    }
                }
                .name,.money{
                    height: 0.48rem;
                    line-height: 0.48rem;
                }
                .money{
                    margin-right: 1rem;
                }
            }
        }

    }
    .notAvailable{
        font-size: 0.3rem;
        text-align: center;
        color: #b2b2b2;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        margin-top: -1.06rem;
        img{
            width: 1.2rem;
            height: 1.2rem;
            margin: 0 auto;
            margin-bottom: 0.6rem;
        }
    }
}
.quit{
    &:after{
